.reminder {
    display:flex;
    flex-direction: column;
    background-color: white;
    width: 300px;
    height: 300px;
    animation: 0.2s ease-in-out appear;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    overflow: hidden;
    overflow-y: scroll;
    scroll-timeline-name: --record-timeline;
}

.reminder::-webkit-scrollbar {
    width: 0;
}

.reminder::-webkit-scrollbar-track {
    display: none;
}

.reminder::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: rgba(127, 145, 154, 0);
    opacity: 0;
}

.reminder:hover::-webkit-scrollbar-thumb {
    background-color: rgba(127, 145, 154, 0.5);
}

@keyframes reminder-control-transparency {
    from {
        background-color: rgba(218, 230, 250, 1);
        -webkit-backdrop-filter: blur(5px);
    }
    to {
        background-color: rgba(195, 207, 226, 0.1);
        -webkit-backdrop-filter: blur(0);
    }
}

.reminder-control {
    display: flex;
    flex-direction: row;
    padding: 5px;
    position: sticky;
    top: 0;
    width: 290px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    background-color: rgba(218, 230, 250, 1);

    backdrop-filter: blur(5px);

    animation: reminder-control-transparency linear;
    animation-timeline: scroll();
}

.reminder-control > input {
    font-size: 18px;
    font-weight: bold;
    min-width: 0px;
}
.reminder-control > button:last-child {
    margin-left: 5px;
}

@keyframes appear {
    from {
        transform: scale(0);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes rem-disappear {
    to {
        transform: scale(0);
        opacity: 0;
    }
    from {
        transform: scale(1);
        opacity: 1;
    }
}

.reminder.closing {
    animation: 0.2s ease-in-out rem-disappear;
    opacity: 0;
}
