.record {
    display: flex;
    flex-direction: row;
    outline: none;
    border: none;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    transition: 0.2s all ease-in-out;
}

.record.filler, .record.filler * {
    pointer-events: none;
    opacity: 0;
}

@keyframes disappear {
    from {

        opacity: 1;
        background: rgba(0, 0, 0, 0);
    }
    to {

        opacity: 0;
        background: #90ff90;
    }
}


.record input {
    width: 90%;
    word-break: break-word;
    font-size: 16px;
}

.record.accomplished {
    animation: 0.3s disappear ease-in-out;
    background: #90ff90;
    opacity: 0;
}
