.notes-header {
    background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    display: flex;
    margin: 10px;
    max-width: 70%;
    font-weight: bold;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 5px;
    padding-top: 2px;
    padding-bottom: 2px;
    animation: 0.6s header-appear ease-in-out;
    align-items: center;
}

@keyframes header-appear {
    from {
        transform: translateY(-200%);
    }

    to {
        transform: translateY(0);
    }

}

.notes-header > button:not(:last-child) {
    margin-right: 5px;
}

.notes-header h3 {
    margin-right: 20px;
    line-height: 0;
}
