.modal-cover {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    animation: 1s modal-cover-appear ease-in-out;
}

body:has(.modal-cover) {
    overflow: hidden;
}

.modal {
    width: 300px;
    padding: 10px;
    background: white;
    border-radius: 8px;
    animation: 1s modal-appear ease-in-out;
}

@keyframes modal-cover-appear {
    from {
        background: rgba(0, 0, 0, 0);
    }

    to {
        background: rgba(0, 0, 0, 0.2);
    }
}

@keyframes modal-cover-disappear {
    to {
        background: rgba(0, 0, 0, 0);
    }

    from {
        background: rgba(0, 0, 0, 0.2);
    }
}

@keyframes modal-appear {
    from {
        transform: translateY(calc(-50vh - 55%));
    }
    to {
        transform: translateY(0);
    }

}

@keyframes modal-disappear {
    to {
        transform: translateY(calc(-50vh - 55%));
    }
    from {
        transform: translateY(0);
    }

}

.modal-cover:has(.modal.closing) {
    animation: 0.6s modal-cover-disappear ease-in-out;
    background: rgba(0, 0, 0, 0);

}

.modal.closing {
    animation: 0.6s modal-disappear ease-in-out;
    transform: translateY(calc(-50vh - 55%));
}

