.nav__button {
    border: none;
    outline: none;
    font-weight: bold;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: 5px;
    padding: 5px;
    transition: 0.1s ease-in-out;
    background-color: rgba(255, 255, 255, 0.4);
    height: 30px;
    cursor: pointer;
}

.nav__button svg {
    justify-self: center;
}

.nav__button:has(svg) {
    width: 30px;
    padding: 6px;
}

.nav__button:hover {
    background-color: rgba(0, 0, 0, 0);
}