.reminders-list {
    display: flex;
    min-width: 80%;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.reminders-list > div {
    margin-right: 10px;
    margin-bottom: 10px;
}

@keyframes appear {
    from {
        opacity: 0;
        transform: scale(0.4)  perspective(10px);

    }
    to {
        opacity: 1;
        transform: scale(1) perspective(10px);

    }
}

@keyframes label-appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.reminders-list .r-label {
    animation: 1s label-appear ease-in-out;
}